<template>
	<div>
		<div id="remotetracks" style="width: 640px;"></div>
		<button @click="joinRoom"></button>
	</div>
</template>

<script>
	import axios from 'axios'
	import * as QNRTC from 'pili-rtc-web'
	import LiveModel from '../model/live.js'
	export default {
		data() {
			return {
				live: null,
				roomToken: '',
				myRTC: null
			};
		},
		created() {
			QNRTC.log.setLevel("disable")
			this.live = new LiveModel()
			
			this.myRTC = new QNRTC.TrackModeSession();
		},
		methods: {
			async joinRoom() {
				try {
					const res = await this.live.getRoomToken('123', 'user')
					this.roomToken = res.data.room_token
					const users = await this.myRTC.joinRoomWithToken(this.roomToken);
					console.log('! :', users)
					
					this.autoSubscribe(this.myRTC)
					/* this.showBtn = false */
					// 
					/* this.localVideo() */
				} catch (error) {
					console.log('', error)
				}
			},

			async subscribe(myRoom, trackInfoList) {
				//  trackId  Track  Track 
				const remoteTracks = await myRoom.subscribe(trackInfoList.map(info => info.trackId));

				// 
				const remoteElement = document.getElementById("remotetracks");
				//  Track play 
				for (const remoteTrack of remoteTracks) {
					remoteTrack.play(remoteElement);
				}
			},

			autoSubscribe(myRoom) {
				const trackInfoList = myRoom.trackInfoList;
				console.log("room current trackInfo list", trackInfoList)

				//  subscribe 
				//  async/await Promise
				this.subscribe(myRoom, trackInfoList)
					.then(() => console.log("subscribe success!"))
					.catch(e => console.error("subscribe error", e));

				//  Track  trackInfo 
				myRoom.on("track-add", (trackInfoList) => {
					console.log("get track-add event!", trackInfoList);
					this.subscribe(myRoom, trackInfoList)
						.then(() => console.log("subscribe success!"))
						.catch(e => console.error("subscribe error", e));
				});
				//  DOM  on 
			}
		},
	}
</script>

